.weather__city {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  position: relative;
  background-repeat: no-repeat;
}

.ukraine-weather__city {
  flex-basis: 18%;
  max-width: 18%;
  height: 19rem;
  background: #ffffff;
  border-radius: 1rem;
  margin-top: 1.2rem;
  padding-left: 3rem;
  padding-right: 3rem;
  background-position: 113% -11%;
  background-repeat: no-repeat;

  &-name,
  &-describe {
    font-size: 1.5rem;
    display: inline;
    word-break: break-word;
    font-weight: 700;
    align-self: start;
  }

  &-country {
    font-size: 1rem;
    align-self: start;
    margin-top: -1.2rem;
    color: #c8c8c8;
  }

  &-temp {
    font-size: 4rem;
    font-weight: bold;
    line-height: 1;
  }

  &-advance {
    font-size: 1rem;
    position: absolute;
    bottom: 0.8rem;
    right: 0.8rem;
    margin: 0;
    cursor: pointer;
    transition: color 0.2s;

    &:hover {
      color: #6b6565;
    }
  }

  &-pressure,
  &-humidity,
  &-wind {
    font-size: 1.1rem;
    font-weight: 600;
    margin: 0;
    margin-bottom: 1rem;
  }

  .loading {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-image: url("../../images/loading.png");
    background-repeat: no-repeat;
    background-size: 50%;
    background-position: center center;
    animation: loading 4s infinite linear;
  }

  @keyframes loading {
    from {
      transform: rotate(0deg);
    }

    from {
      transform: rotate(360deg);
    }
  }
}
