.worldwide-weather__city {
  max-width: 80%;
  min-height: 80vh;
  background: #ddd;
  border-radius: 2rem;
  margin: 1.2rem auto 0 auto;
  padding-left: 5rem;
  padding-right: 5rem;
  background-position: 99% 0%;
  background-repeat: no-repeat;

  &-name,
  &-describe {
    font-size: 3.5rem;
    display: inline;
    word-break: break-word;
    font-weight: 700;
    align-self: start;
    margin: 0;
  }

  &-country {
    font-size: 1.5rem;
    align-self: start;
    margin-top: -4.2rem;
    color: #777777;
  }

  &-temp {
    font-size: 8rem;
    font-weight: bold;
    line-height: 1;
  }

  &-advance {
    font-size: 2rem;
    position: absolute;
    bottom: 0.8rem;
    right: 0.8rem;
    margin: 0;
    cursor: pointer;
    transition: color 0.2s;

    &:hover {
      color: #6b6565;
    }
  }

  &-pressure,
  &-humidity,
  &-wind {
    font-size: 2.1rem;
    font-weight: 600;
    margin: 0;
  }
}
